@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
*{
  box-sizing: border-box;

}
body{
  background-color: #111;
  color:#fbfbfb;
  margin:0;
  font-family: 'Roboto', sans-serif;
 
}

.lists{
  margin-top:-7rem;
}

footer{
  
  margin: 4rem 0;
  text-align: center;


}
footer a{
  color: inherit;
  text-decoration: none;
}

.loading{
  position: fixed;
  top:0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 99;
  background: #000;
  display: flex;
  justify-content: center;
  align-items: center;
 
}
.movieRow {
  margin-bottom: 2rem;

}

.movieRow h2{
  margin: 0 0 0 2rem;
}

.movieRow--listarea{
  overflow-x: hidden;
  padding-left: 2rem;

}
.movieRow--list{
  transition: all 0.3s ease-in-out;
}
.movieRow--item{
  display: inline-block;
  width: 13rem;
  cursor: pointer;
}

.movieRow--item img{
  width: 100%;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  transition: all 0.2s ease;
}
.movieRow--item img:hover{
  -webkit-transform: scale(1);
          transform: scale(1);
}

.movieRow--before,
.movieRow--next{
  position:absolute;
  width: 3rem;
  height: 19.5rem;
  background: rgba(0, 0, 0, 0.8);
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow:hidden;
  cursor: pointer;
  border-radius: .2rem;
  opacity: 0;
  transition: all .5s ease;
}

.movieRow--before{
  left: 0;
}
.movieRow--next{
  right: 0;
}

.movieRow:hover .movieRow--before,
.movieRow:hover .movieRow--next{
  opacity: 1;

}


@media only screen and (max-width: 760px){
  .movieRow--before,
  .movieRow--next{
    opacity: 1;
  }
}
/*master*/
.featured{
  height: 100vh;

}

.featured--vertical{
  width: inherit;
  height: inherit;
  background: linear-gradient(to top, #111 10%, transparent 90%);
}

.featured--horizontal{
  width: inherit;
  height: inherit;
  background: linear-gradient(to right, #111 30%, transparent 70% );
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 2rem;
  padding-bottom: 10rem;
  padding-top: 4.375rem;
}

/*infos*/

.featured--name{
  font-size: 3.75rem;
  font-weight: bold;
  max-width: 40%;
  
}

.featured--info{
  font-size: 1.125rem;
  font-weight: bold;
  margin-top: 1rem;
  max-width: 40%;
}

.featured--info .featured--points,
.featured--info .featured-year,
.featured--info .featured--seasons{
  margin-right: 1rem;
  display: inline-block;
}
.featured--info .featured--points{
  color:#46d369;
}
.featured--info .featured--description{
  margin-top: 1rem;
  font-size: 1.25rem;
  color:#999;
  
  
}

/*botoes-buttons*/
.featured--info .featured--buttons{
  margin-top: 1rem;
}
.featured--info .featured--watchButton,
.featured--info .featured--myListButton{
  display: inline-block;
  font-size: 1.25rem;
  font-weight: bold;
  padding: .75rem 1.75rem;
  border-radius: .5rem;
  text-decoration: none;
  margin-right: 1rem;
  opacity: 1;
  transition: all ease 0.2s;
}

.featured--info .featured--watchButton{
  background: #f2f2f2;
  color: #111;
}
.featured--info .featured--myListButton{
  background: #333;
  color: #f2f2f2;
}

 .featured--info .featured--watchButton:hover,
.featured--info .featured--myListButton:hover{
  opacity: 0.8;
}

/*generos-genres*/
.featured--info .featured--genres{
  margin-top: 1rem;
  font-size: 1.25rem;
  color:#999
}



@media only screen and (max-width: 760px){
  .featured{
    height: 70vh;
    width: 100vw;

  }

  .featured--name{
    font-size: 2.5rem;
    max-width: 100%;
    margin-top: 3rem;
  }
  .featured--info{

    max-width: 100vw;
    font-size: 1rem;
    height: 90vh;
    width: 100%;
  }

  .featured--info .featured--description{
    font-size: 1rem;
    max-width: 100vw;
    margin-right: 2rem;
 
  }

  .featured--info .featured--watchButton,
  .featured--info .featured--myListButton{ 
    font-size: 1rem;
    padding: 1rem 2rem;
  }

  .featured--info .featured--genres{
    font-size: 1rem;
  }


}
header{
  padding: 0 2rem;
  position: fixed;
  z-index: 999;
  top:0;
  left:0;
  right:0;
  height: 4.375rem ;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: transparent;
  transition: all ease 0.5s;
}
header.black{
  background: #141414;
}


.header--logo{
  height: 2rem;
}

.header--logo img{
  height: 100%;
}

.header--user{
  height: 2.2rem;
}

.header--user img{
  height: 100%;
  border-radius: .2rem;
}
